<template>
  <div class="findpassword">
    <van-nav-bar title="找回密码" left-text="返回" left-arrow :fixed="true" @click-left="back" />

    <van-form @submit="commit">
      <van-field
        v-model="userInfo.phone"
        name="手机号"
        label="手机号"
        placeholder="11位手机号"
        autocomplete="off"
      />
      <van-field
        v-model="userInfo.password"
        type="text"
        name="新密码"
        label="新密码"
        placeholder="密码(6-16个字符)"
        autocomplete="off"
      />
      <div class="btn-box">
        <van-button round block type="info" native-type="submit">提交</van-button>
      </div>
    </van-form>
  </div>
</template>

<script>
//导入表单验证模块
import { valid } from "../utils/valid";

import { utils } from "../utils/utils";
export default {
  name: "FindPassword",
  data() {
    return {
      userInfo: {
        phone: "",
        password: ""
      }
    };
  },
   computed: {
     isLogin() {
      return this.$store.state.isLogin;
    }
   },
  methods: {
    back() {
      this.$router.go(-1);
    },
    commit() {
      if (!this.isLogin) {
      this.$toast('请先登录');
      return;
    }
      //表单验证
      if (valid.validForm(this.userInfo, this)) {
        //验证通过

        //加载提示
        this.$toast.loading({
          forbidClick: true,
          duration: 0,
          message: "加载中..."
        });

        this.axios({
          method: "POST",
          url: "/retrievePassword",
          data: {
            appkey: this.appkey,
            phone: this.userInfo.phone,
            password: this.userInfo.password
          },
          transformRequest: utils.transformRequest
        })
          .then(result => {
            this.$toast.clear();

            if (result.data.code == "L001") {
              this.$router.push({ name: "Login" });
            } else {
              this.$toast(result.data.msg);
            }
          })
          .catch(err => {
            this.$toast.clear();
          });
      }
    }
  }
};
</script>

<style lang="less" scoped>
.findpassword {
  padding-top: 96px;
  .van-nav-bar {
    background-color: #ab7c37;
    // margin-bottom: 10px;
  }
  .van-nav-bar .van-icon {
    color: #fff;
    background-color: #ab7c37;
  }
  .van-nav-bar__text {
    color: #fff;
    background-color: #ab7c37;
  }
  .van-nav-bar__title {
    color: #fff;
  }
  .van-button--info {
    background-color: #ab7c37;
    border-color: #ab7c37;
  }
  .btn-box {
    margin-top: 16px;
    padding: 0 10px;
  }
}
</style>